'use client';

import React, { useMemo } from 'react';
import { 
  Landmark, ArrowRight, ShieldCheck, Zap, 
  Lock, Cpu, FileText, Sparkles, CheckCircle2 
} from 'lucide-react';
import Link from 'next/link';
import { useParams } from 'next/navigation';

export default function DynamicTreatyLab() {
  const params = useParams();
  const slug = params.slug as string;

  // Logika untuk mengubah slug (contoh: tax-treaty-usa-to-indonesia) 
  // menjadi Nama Negara yang rapi (contoh: USA - INDONESIA)
  const jurisdictionLabel = useMemo(() => {
    if (!slug) return 'GLOBAL JURISDICTION';
    
    // Menghapus kata "tax-treaty-" dan mengganti "-" dengan spasi
    const names = slug.replace('tax-treaty-', '').split('-to-');
    if (names.length === 2) {
      return `${names[0].toUpperCase()} - ${names[1].toUpperCase()}`;
    }
    return slug.replace(/-/g, ' ').toUpperCase();
  }, [slug]);

  const targetCountry = jurisdictionLabel.split(' - ')[1] || 'Target Country';

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32 font-sans">
      
      {/* 1. HERO SECTION - ELITE DESIGN */}
      <section className="max-w-6xl mx-auto pt-32 pb-16 px-8 text-center space-y-8">
        <div className="inline-flex items-center gap-3 px-6 py-2 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.4em]">
          <Landmark size={16} /> Jurisdiction: {jurisdictionLabel}
        </div>
        
        <h1 className="text-7xl font-serif italic leading-tight text-black">
          Simulation <span className="text-[#D4AF37]">Laboratory.</span>
        </h1>
        
        <p className="max-w-3xl mx-auto text-gray-400 font-light italic text-[20px] leading-relaxed">
          Deep-dive fiscal environment for {jurisdictionLabel}. Analyze every article of the bilateral tax treaty with high-fidelity AI simulations.
        </p>
      </section>

      {/* 2. MAIN INTERFACE (LOCKED STATE) */}
      <section className="max-w-5xl mx-auto px-8">
        <div className="relative group">
          {/* Overlay Pesan Akses Terkunci (Trigger Penjualan Pro) */}
          <div className="absolute inset-0 bg-white/40 backdrop-blur-[8px] z-20 rounded-[4rem] border border-gray-100 flex items-center justify-center p-12">
            <div className="max-w-xl bg-black text-white p-12 rounded-[3rem] shadow-2xl text-center space-y-8 border border-[#D4AF37]/30">
              <div className="w-20 h-20 bg-[#D4AF37]/10 rounded-full flex items-center justify-center mx-auto border border-[#D4AF37]/20">
                <Lock className="text-[#D4AF37]" size={32} />
              </div>
              <div className="space-y-4">
                <h2 className="text-3xl font-serif italic">Elite Access Required</h2>
                <p className="text-gray-400 font-light text-[15px] leading-relaxed italic">
                  This specialized simulation for <span className="text-white font-bold">{jurisdictionLabel}</span> is restricted to Elite members. Get full article-by-article analysis and PDF Intelligence Reports.
                </p>
              </div>
              <div className="flex flex-col md:flex-row gap-4 justify-center">
                <Link href="/pro" className="px-10 py-5 bg-[#D4AF37] text-black rounded-2xl text-[13px] font-black uppercase tracking-widest hover:bg-white transition-all shadow-xl flex items-center gap-3">
                  Unlock Simulation <Zap size={16} />
                </Link>
              </div>
            </div>
          </div>

          {/* Background Simulasi (Tampilan samar-samar agar user tertarik) */}
          <div className="bg-white border border-gray-100 rounded-[4rem] p-16 shadow-sm opacity-20 select-none pointer-events-none">
            <div className="flex items-center gap-6 mb-12 border-b border-gray-50 pb-8">
              <div className="w-12 h-12 bg-gray-100 rounded-xl"></div>
              <div className="h-4 w-64 bg-gray-100 rounded-full"></div>
            </div>
            <div className="grid grid-cols-3 gap-8 mb-12">
               <div className="h-32 bg-gray-50 rounded-3xl"></div>
               <div className="h-32 bg-gray-50 rounded-3xl"></div>
               <div className="h-32 bg-gray-50 rounded-3xl"></div>
            </div>
          </div>
        </div>
      </section>

      {/* 3. PRO BENEFITS GRID */}
      <section className="max-w-6xl mx-auto px-8 mt-32 space-y-20">
        <div className="text-center space-y-4">
          <h2 className="text-4xl font-serif italic text-black">Professional Capabilities</h2>
          <p className="text-gray-400 font-light italic text-[18px]">Transform complex treaty laws into actionable business intelligence.</p>
        </div>

        <div className="grid md:grid-cols-3 gap-12">
          <div className="p-10 bg-white border border-gray-50 rounded-[3rem] space-y-6 border-b-4 border-b-[#D4AF37] shadow-sm">
            <Cpu className="text-[#D4AF37]" size={32} />
            <h3 className="text-xl font-serif italic text-black">AI Article Dissection</h3>
            <p className="text-gray-400 text-[14px] leading-relaxed font-light">
              AI analysis of every single article within the treaty. Learn how to legally qualify for the lowest possible withholding rates.
            </p>
          </div>

          <div className="p-10 bg-white border border-gray-50 rounded-[3rem] space-y-6 border-b-4 border-b-[#D4AF37] shadow-sm">
            <FileText className="text-[#D4AF37]" size={32} />
            <h3 className="text-xl font-serif italic text-black">Full PDF Report</h3>
            <p className="text-gray-400 text-[14px] leading-relaxed font-light">
              Generate a professional Intelligence Report for {targetCountry} compliance, perfect for board meetings or tax audit preparation.
            </p>
          </div>

          <div className="p-10 bg-white border border-gray-50 rounded-[3rem] space-y-6 border-b-4 border-b-[#D4AF37] shadow-sm">
            <ShieldCheck className="text-[#D4AF37]" size={32} />
            <h3 className="text-xl font-serif italic text-black">Permanent Establishment</h3>
            <p className="text-gray-400 text-[14px] leading-relaxed font-light">
              Simulate PE (Permanent Establishment) risks for your digital or physical presence in {targetCountry} jurisdictions.
            </p>
          </div>
        </div>
      </section>

      {/* 4. FOOTER CTA */}
      <section className="max-w-4xl mx-auto mt-40 px-8 text-center bg-black p-20 rounded-[4rem] text-white">
        <Sparkles className="text-[#D4AF37] mx-auto mb-8" size={48} />
        <h2 className="text-4xl font-serif italic mb-6 text-white">Ready for Elite Intelligence?</h2>
        <p className="text-gray-400 font-light mb-12 max-w-2xl mx-auto italic leading-relaxed">
          Join thousands of tax professionals using GlobalTax Laboratory for the most accurate fiscal modeling in 2026.
        </p>
        <Link href="/pro" className="px-14 py-6 bg-[#D4AF37] text-black rounded-2xl text-[14px] font-black uppercase tracking-[0.3em] hover:bg-white transition-all">
          Get Elite Access
        </Link>
      </section>
    </div>
  );
}